
@use '../../abstracts' as *;
@use '../../base' as *;
@use '../../layout' as *;
@use '../anchors' as *;

.global-footer {
    background: $color-nearblack;
    color: $color-purewhite;
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
    padding-top: $measure--medium3;
    @include alphatype();
    @include media('>=small') {
        @include standard-box();
        @include x-column-grid(24);
    }
}

.global-footer__container {
    padding-bottom: $measure--small4;
    @include default-padding();
}

.global-footer__header-logo {
    color: $color-purewhite;
    font-weight: $font-weight-book;
    letter-spacing: -0.5px;
    font-size: $measure--small4;
    display: flex;
    justify-content: center;
    @include media('>=between') {
        justify-content: flex-start;
    }
    a {
        @include font-family('heading');
        @include heading-font-features();
        &:visited {
            color: $color-purewhite;
        }
        
        &:focus, &:active {
            @include outline-properties($color-purewhite);
        }
    }
    

    span.global-footer__logo-style {
        font-weight: $font-weight-bold;
        letter-spacing: -0.5px;
    }
}

.global-footer__social-media {
    display: flex;
    align-items: self-end;
    justify-content: center;
    @include media('>=tablet') {
        grid-column: 1 / -1
    }
    @include media('>=between') {
        grid-column: auto / auto;
        justify-content: flex-end;
    }
}

.global-footer__social-media__icons {
    display: flex;
    flex-direction: row;
    white-space: nowrap;
    .global-footer__social-media__icon {
        margin-right: $measure--small2;
        &:last-of-type {
            margin-right: 0;
        }
    }
}

.global-footer__top {
    grid-template-columns: 1fr;
    text-align: center;

    @include media('>=tablet') {
        text-align: left;
        grid-template-columns: repeat(3, auto);
    }

    @include media('>=between') {
        grid-template-columns: repeat(4, auto);
        
    }
    @include media('>=small') {
        grid-template-columns: repeat(3, 1fr) auto;
    }
}

.global-footer__btm {
    grid-template-columns: 1fr;
    text-align: center;
    @include media('>=tablet') {
        text-align: left;
        grid-template-columns: 75% 1fr;
    }
}
.global-footer__btm__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    @include media('>=tablet') {
        justify-content: flex-end;
    }
}
.global-footer__row {
    display: grid;
    gap: $measure--basic;
    margin-top: $measure--basic;
}


.global-footer__useful-links {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    margin:$measure--basic 0;
    justify-content: center;
    @include media('>=tablet') {
        justify-content: flex-start;
    }
    li {
        border-right: thin solid white;
        margin-right: $measure--basic;
        padding-right: $measure--basic;
        &:last-of-type { 
            border-right: none;
        }
    }
}

.global-footer__content__text__copyright {
    font-weight: $font-weight-bold;
}

.global-footer__col {
    ul {
        list-style-type: none;
    }
}

hr {
    border-style: solid;
    border-color: $color-ford-grey;
    margin-top: $measure--medium3;
}