@use '../../abstracts' as *;
@use '../../base' as *;
@use '../anchors' as *;

.no-js, .js {
    .menu {
        @include media('>=small') {

            .menu__content { 
                
                .menu__content__header {
                    display: none;
                }
            }
            .menu__display__button {
                display: none;
            }
        }
    }
}

.js .global-header {
    .menu {
        @include media('>=small') {
            z-index: z-index('navigation');
            position: relative;

            .menu__content {
                
                display: inherit;

                .menu__level1 {
                    display: flex;
                    flex-direction: row;

                    ul {
                        position: absolute;
                        width: 100%;
                        overflow: hidden;
                    }
                }
            }

            > .menu__display__button {
                > button {
                    @include cta--basic--subelement(
                        $color-purewhite,
                        $color-gray10,
                        $color-purewhite,
                        $color-gray8,
                        $color-purewhite,
                        square
                    );
                    
                    & {
                        display: flex;
                        padding-bottom: 0;
                        padding-left: 0;
                        padding-right: 0;
                        padding-top: 0;
                    }

                    &:active,
                    &:focus {
                        @include outline-properties($color-nearblack);
                    }

                    .menu__button__icon {
                        &:before {
                            content: '\f00d';
                        }
                    }
                }
            }

            > .menu__content {          
                display: flex;

                ul {
                    li {
                        position: relative;
                        &[data-open='true'] {
                            > ul {
                                visibility: visible;
                                max-height: inherit;
                            }
                        }
                    }

                    .menu-animation__slideOpen > ul {
                        @include menu-animation-slideOpen;
                    }
                }
            }
            
            display: block;
    
            .menu__level1,
            .menu__level2 {
                list-style: none;
                margin-left: 0;
                margin-top: 0;
                max-width: unset;
    
                & > li > .menu__controls {
                    & > button {
                        .menu__button__icon {
                            &:before {
                                content: '\f078';
                                font-weight: $font-weight-light;
                            }
                        }
    
                        .menu__button__text {
                            @include screenreader-only();
                        }
    
                        &[aria-expanded='true'] {
                            .menu__button__icon {
                                &:before {
                                    content: '\f077';
                                }
                            }
                        }
                    }
                }
    
                li {
                    &.menu__current > .menu__controls > a > span {
                        display: inline;
                        border-bottom: $border-width-default $border-style-solid
                            $color-red50;
                    }
                }
            }
    
            .menu__level1,
            .menu__level2 {
                li {
                    @include content-style();
                    @include font-family('heading');
                    @include heading-font-features();
    
                    & {
                        font-weight: $font-weight-normal;
                        max-width: unset;
                    }
    
                    &.menu__hierarchy > .menu__controls > a {
                        span {
                            border-bottom: $border-width-default $border-style-solid
                                $color-gray2;
                        }
                    }
    
                    &.menu__current > .menu__controls > span:focus {
                        outline: none;
    
                        > span {
                            @include outline-properties($color-purewhite);
                        }
                    }
                }
            }
    
            .menu__level1 {
                background-color: $color-nearblack;
                color: $color-purewhite;
    
                > li {
                    @include betatype();
    
                    & {
                        line-height: $line-height-ratio--body;
                    }
                }
    
                & > li {
                    > .menu__controls {
                        
                        & > a {
                            @include navigation-anchor(
                                $color: $color-purewhite,
                                $background: $color-nearblack,
                                $hover-color: $color-purewhite,
                                $hover-background: $color-gray8,
                                $outline: $color-purewhite
                            );
                        }
    
                        & > button {
                        @include navigation-button(
                                $color-purewhite,
                                $color-nearblack,
                                $color-gray8,
                                $color-purewhite
                            );
                            @include navigation-button-transition-helper(
                                $color-nearblack
                            );
                            
                        }
    
                        & > a,
                        & > span {
                            padding-left: $measure--half;
                        }
                    }
    
                    &:last-of-type[data-open='false'] {
                        > .menu__controls {
                            border-bottom: none;
                        }
                    }
                }
            }
    
            .menu__level2 {
                background-color: $color-gray9;
                color: $color-purewhite;
    
                > li {
                    @include alphatype();
                    line-height: $line-height-ratio--body;
                }
    
                & > li {
                    > .menu__controls {
                        border-bottom: $border-width-hairline $border-style-solid
                            $color-gray7;
    
                        & > a {
                            @include navigation-anchor(
                                $color: $color-purewhite,
                                $background: $color-gray9,
                                $hover-color: $color-purewhite,
                                $hover-background: $color-gray7,
                                $outline: $color-purewhite
                            ); 
                        }
    
                        & > button {
                            @include navigation-button(
                                $color-purewhite,
                                $color-gray9,
                                $color-gray7,
                                $color-purewhite
                            );
                            @include navigation-button-transition-helper(
                                $color-gray9
                            );
                        }
    
                        & > a,
                        & > span {
                            padding-left: #{2 * $measure--half};
                        }
                    }
    
                    &:last-of-type[data-open='false'] {
                        > .menu__controls {
                            border-bottom: none;
                        }
                    }
                }
            }
    
            .menu__controls {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                justify-content: space-between;
                padding-right: $measure--half;
            }
    
            a,
            .menu__current > .menu__controls > span {
                display: block;
                padding: $measure--half;
                width: 100%;
            }
    
            li {
                margin: 0;
            }
        }
    }
}

