@use '../../abstracts' as *;
@use '../../base' as *;
@use '../anchors' as *;

////
/// Menu
/// @group patterns
////

.global-header {
    .menu__display__button {
        display: flex;
        justify-content: flex-end;
    }
}


/* burger bun */
.menu {
    .burger-bun {
        display: block;

        span {
            display: block;
            width: 33px;
            height: 4px;
            margin-bottom: 5px;
            position: relative;
            background: $color-purewhite;
            border-radius: 3px;
            z-index: z-index('above');
            transform-origin: 4px 0px;
            transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
                background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
                opacity 0.55s ease;

            &:nth-last-child(1) {
                transform-origin: 0% 100%;
            }
        }
    }
}

.js .menu[data-open='true'] {
    .burger-bun {
        span {
            opacity: 1;
            transform: rotate(45deg) translate(7px, -2px);
            background: $color-purewhite;
        }

        span:nth-last-child(2) {
            opacity: 0;
            transform: rotate(0deg) scale(0.2, 0.2);
        }

        span:nth-last-child(1) {
            transform: rotate(-45deg) translate(1px, 8px);
        }
    }
}

.no-js .menu {
    .burger-bun input {
        &:checked ~ span {
            opacity: 1;
            transform: rotate(45deg) translate(7px, -2px);
            background: $color-purewhite;
        }

        &:checked ~ span:nth-last-child(2) {
            opacity: 0;
            transform: rotate(0deg) scale(0.2, 0.2);
        }

        &:checked ~ span:nth-last-child(1) {
            transform: rotate(-45deg) translate(1px, 8px);
        }
    }
}
/* end burger bun */

.menu {
    @include media('<small') {
        display: block;

        .menu__level1,
        .menu__level2 {
            list-style: none;
            margin-left: 0;
            margin-top: 0;
            max-width: unset;

            & > li > .menu__controls {
                & > button {
                    .menu__button__icon {
                        &:before {
                            content: '\f078';
                            font-weight: $font-weight-light;
                        }
                    }

                    .menu__button__text {
                        @include screenreader-only();
                    }

                    &[aria-expanded='true'] {
                        .menu__button__icon {
                            &:before {
                                content: '\f077';
                            }
                        }
                    }
                }
            }

            li {
                &.menu__current > .menu__controls > a > span {
                    display: inline;
                    border-bottom: $border-width-default $border-style-solid
                        $color-red50;
                }
            }
        }

        .menu__level1,
        .menu__level2 {
            li {
                @include content-style();
                @include font-family('heading');
                @include heading-font-features();

                & {
                    font-weight: $font-weight-normal;
                    max-width: unset;
                }
                

                &.menu__hierarchy > .menu__controls > a {
                    span {
                        border-bottom: $border-width-default $border-style-solid
                            $color-gray2;
                    }
                }

                &.menu__current > .menu__controls > span:focus {
                    outline: none;

                    > span {
                        @include outline-properties($color-purewhite);
                    }
                }
            }
        }

        .menu__level1 {
            background-color: $color-gray10;
            color: $color-purewhite;

            > li {
                @include betatype();

                & {
                    line-height: $line-height-ratio--body;
                }
                
            }

            & > li {
                > .menu__controls {
                    border-bottom: $border-width-hairline $border-style-solid
                        $color-gray8;

                    & > a {
                        @include navigation-anchor(
                            $color: $color-purewhite,
                            $background: $color-gray10,
                            $hover-color: $color-purewhite,
                            $hover-background: $color-gray8,
                            $outline: $color-purewhite
                        );
                        
                    }

                    & > button {
                       @include navigation-button(
                            $color-purewhite,
                            $color-gray10,
                            $color-gray8,
                            $color-purewhite
                        );
                        @include navigation-button-transition-helper(
                            $color-gray10
                        );
                        
                    }

                    & > a,
                    & > span {
                        padding-left: $measure--half;
                    }
                }

                &:last-of-type[data-open='false'] {
                    > .menu__controls {
                        border-bottom: none;
                    }
                }
            }
        }

        .menu__level2 {
            background-color: $color-gray9;
            color: $color-purewhite;

            > li {
                @include alphatype();
                line-height: $line-height-ratio--body;
            }

            & > li {
                > .menu__controls {
                    border-bottom: $border-width-hairline $border-style-solid
                        $color-gray7;

                    & > a {
                        @include navigation-anchor(
                            $color: $color-purewhite,
                            $background: $color-gray9,
                            $hover-color: $color-purewhite,
                            $hover-background: $color-gray7,
                            $outline: $color-purewhite
                        );
                        
                    }

                    & > button {
                        @include navigation-button(
                            $color-purewhite,
                            $color-gray9,
                            $color-gray7,
                            $color-purewhite
                        );
                        @include navigation-button-transition-helper(
                            $color-gray9
                        );
                        
                    }

                    & > a,
                    & > span {
                        padding-left: #{2 * $measure--half};
                    }
                }

                &:last-of-type[data-open='false'] {
                    > .menu__controls {
                        border-bottom: none;
                    }
                }
            }
        }

        .menu__controls {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
            padding-right: $measure--half;
        }

        a,
        .menu__current > .menu__controls > span {
            display: block;
            padding: $measure--half;
            width: 100%;
        }

        li {
            margin: 0;
        }
    }

    @include media('print') {
        display: none;
    }
}

.no-js .menu {
    //t display: none;
    
}

.js {
    .menu {
        @include media('>=small') {
            //t display: none;
        }

        .menu__content {
            display: none;

            @include media('>=small') {
                //display: inherit;
            }

            .menu__level1 {
                ul {
                    visibility: hidden;
                    max-height: 0;
                }

                .menu-animation__slideClose > ul {
                    @include menu-animation-slideClose;
                }
            }
        }

        &[data-open='true'] {
            z-index: z-index('navigation');

            > .menu__display__button {
                > button {
                    @include cta--basic--subelement(
                        $color-purewhite,
                        $color-gray10,
                        $color-purewhite,
                        $color-gray8,
                        $color-purewhite,
                        square
                    );
                    
                    & {
                        display: flex;
                        padding-bottom: 0;
                        padding-left: 0;
                        padding-right: 0;
                        padding-top: 0;
                    }

                    &:active,
                    &:focus {
                        @include outline-properties($color-nearblack);
                    }

                    .menu__button__icon {
                        &:before {
                            content: '\f00d';
                        }
                    }
                }
            }

            > .menu__content {
                box-shadow: 0 $measure--half $measure--small1 0
                    rgba(0, 0, 0, 0.3);
                display: flex;
                flex-direction: column;
                flex-wrap: nowrap;
                left: 0;
                position: absolute;
                right: 0;

                ul {
                    li {
                        &[data-open='true'] {
                            & > .menu__controls > a > span,
                            & > .menu__controls > span > span {
                                font-weight: $font-weight-bold;
                            }

                            > ul {
                                visibility: visible;
                                max-height: inherit;
                            }
                        }
                    }

                    .menu-animation__slideOpen > ul {
                        @include menu-animation-slideOpen;
                    }
                }
            }
        }

        > .menu__display__button {
            padding-bottom: $measure--half;
            padding-left: $measure--half;
            padding-right: $measure--basic;

            @include media('>=small') {
                height: 100%;
            }

            .menu__display__button__button {
                @include content-style();
                @include smalltype();

                @include cta--basic--subelement(
                    $color-purewhite, // color
                    inherit, // background
                    $color-purewhite, // hover color
                    inherit, // hover background
                    $color-purewhite, //outline
                    square // corners
                );
                
                & {
                    align-items: center;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: nowrap;
                    height: $measure--medium7;
                    justify-content: center;
                    min-height: $minimum-control-height;
                    padding-bottom: 0;
                    padding-left: 0;
                    padding-right: 0;
                    padding-top: 0;
                    width: $measure--medium7;
                    position: relative;
                }
                
                &:active,
                &:focus {
                    @include outline-properties($color-nearblack);
                }

                .menu__button__icon {
                    font-size: inherit;

                    &:before {
                        content: '\f0c9';
                    }
                }

                .menu__button__text {
                    @include smalltype();

                    font-weight: $font-weight-book;
                }
            }

            .menu__display__button__button__wrapper {
                @include content-style();
                @include betatype();
                & {
                    margin-top: 0;
                }

                @include media('<small') {
                    align-items: center;
                    display: inline-flex;
                    flex-direction: column;
                    justify-content: center;
                }

                @include media('>=small') {
                    min-width: $measure--medium8;
                    align-items: center;
                    display: flex;
                    flex-direction: row;
                }
            }
        }

        .menu__content__header {
            @include content-style();
            @include font-family('heading');
            @include heading-font-features();
            @include betatype();
            & {
                background-color: $color-gray10;
                border-bottom: $border-width-hairline $border-style-solid
                    $color-gray8;
                border-top: $border-width-hairline $border-style-solid $color-gray8;
                color: $color-purewhite;
                font-weight: $font-weight-normal;
                line-height: $line-height-ratio--body;
                max-width: unset;
            }

            a {
                @include navigation-anchor(
                    $color: $color-purewhite,
                    $background: $color-gray10,
                    $hover-color: $color-purewhite,
                    $hover-background: $color-gray8,
                    $outline: $color-purewhite
                );

                > span {
                    border-bottom: $border-width-default $border-style-solid
                        $color-purewhite;
                    display: inline;
                    padding: 0;
                }
            }

            &.menu__content__header--current {
                > a > span {
                    border-bottom: $border-width-default $border-style-solid
                        $color-red50;
                }
            }

            span {
                display: block;
                padding: $measure--half;
                width: 100%;

                > span {
                    border-bottom: $border-width-default $border-style-solid
                        $color-red50;
                    display: inline;
                    padding: 0;
                }

                &:focus {
                    outline: none;

                    > span {
                        @include outline-properties($color-purewhite);
                    }
                }
            }
        }
    }

    .menu__veil {
        @include veil();

        &[data-on='true'] {
            @include media('>=small') {
                //display: none;
            }
        }
    }
}
