@use '../../abstracts' as *;
@use 'anchors' as *;
@use "sass:color"; // usage: color.adjust($color-cta--t1, $lightness: -6%)

@mixin cta--basic(
    $color: $color-purewhite,
    $background: $color-cta--t1,
    $hover-color: $color-purewhite,
    $hover-background: $color-cta-hover--t1,
    $outline: $color-purewhite,
    $color-border: $color-cta--t1,
    $color-border-hover: $color-cta-hover--t1,
    $corners: square,
    $corners-radius: $measure--quarter,
    $box-shadow : false
) {
    @include anchor-reset();
    @include anchor-default-outline($outline);
    & {
        width: fit-content;
        display: inline-block;
        padding-bottom: $measure--half;
        padding-left: $measure--small2;
        padding-right: $measure--small2;
        padding-top: $measure--half;
        transition: background-color $transition-duration--long, transform $transition-duration--long;
        border: $border-width-default $border-style-solid $color-border;

        @if $corners == round {
            border-radius: $corners-radius;
        }

        @if $box-shadow != false {
            box-shadow: $box-shadow;
        }
        
    }

    &:after {
        opacity: 0;
    }

    

    &:visited {
        border: $border-width-default $border-style-solid $color-border;
    }

    &,
    &:focus,
    &:active {
        background-color: $background;
        color: $color;
        border: $border-width-default $border-style-solid $color-border;
        span {
            color: $color;
        }
    }

    &:hover {
        @include media('hover') {
            background-color: $hover-background;
            color: $hover-color;
            border: $border-width-default $border-style-solid $color-border-hover;
            span {
                color: $hover-color;
            }
        }
    }
}

/// default / theme-1 CTA on bright background
.cta,
.cta--basic {
    @include cta--basic(
        $color-purewhite,
        $color-cta--t1,
        $color-purewhite,
        color.adjust($color-cta--t1, $lightness: -6%),
        $color-nearblack,
        $color-cta--t1,
        color.adjust($color-cta--t1, $lightness: -6%),
        round
    )
}

.cta--white {
    @include cta--basic(
        $color-cta--t1, // color
        $color-purewhite, // background
        $color-cta--t1, // hover-color
        color.adjust($color-purewhite, $lightness: -6%), // hover-background
        $color-nearblack, // outline
        $color-purewhite, // color-border
        color.adjust($color-purewhite, $lightness: -6%), // color-border-hover
        round // corners
    )
}

.cta--member {
    @include cta--basic(
        $color-nearblack, // color
        $color-gray80, // background
        $color-nearblack, // hover-color
        color.adjust($color-gray80, $lightness: -6%), // hover-background
        $color-nearblack, // outline
        $color-gray80, // color-border
        color.adjust($color-gray80, $lightness: -6%), // color-border-hover
        round, // corners
        $measure--medium3
    )
}

.cta--book-class {
    @include cta--basic(
        $color-purewhite, // color
        $color-red50, // background
        $color-purewhite, // hover-color
        color.adjust($color-red50, $lightness: -6%), // hover-background
        $color-nearblack, // outline
        $color-red50, // color-border
        color.adjust($color-red50, $lightness: -6%), // color-border-hover
        round, // corners
        $measure--medium3
    )
}

.cta--white--viewpro {
    @include cta--basic(
        $color-cta--t1, // color
        $color-purewhite, // background
        $color-cta--t1, // hover-color
        color.adjust($color-purewhite, $lightness: -6%), // hover-background
        $color-nearblack, // outline
        $color-purewhite, // color-border
        color.adjust($color-purewhite, $lightness: -6%), // color-border-hover
        round, // corners
        $measure--medium3,
        $box-shadow: (0 1px 2px rgba(0, 0, 0, 0.1),   /* subtle top edge */
        0 4px 8px rgba(0, 0, 0, 0.15)  /* deeper bottom-right blur */)
    )
}

@mixin cta--basic--subelement(
    $color: $color-purewhite,
    $background: $color-red50,
    $hover-color: $color-purewhite,
    $hover-background: $color-red70,
    $outline: $color-purewhite,
    $corners: square,
    $class: 'text'
) {
    @include anchor-reset();

    & {
        display: inline-block;
        padding-bottom: $measure--half;
        padding-left: $measure--small2;
        padding-right: $measure--small2;
        padding-top: $measure--half;
        transition: background-color $transition-duration--long;

        @if $corners == round {
            border-radius: $measure--quarter;
        }
    }
    

    &,
    &:focus,
    &:active {
        background-color: $background;
        color: $color;
    }

    &:focus {
        .#{$class} {
            @include outline-properties($outline);
        }
    }

    &:hover {
        @include media('hover') {
            background-color: $hover-background;
            color: $hover-color;
        }
    }
}

