@use '../../abstracts' as *;
@use '../../base' as *;
@use '../../layout' as *;
@use '../anchors' as *;

.global-header__wrap {
    width: 100%;
    position: relative;
    z-index: z-index('unveiled');
}

.global-header__ribbon {
    background-color: $color-red50;
    margin-bottom: $measure--small4;
    padding-top: $measure--basic;
    padding-bottom: $measure--basic;
    
}

.global-header__ribbon__container {
    @include media('>=small') {
        @include standard-box();
    }
}

.global-header__ribbon__message {
    display: flex;
    flex-direction: row;
    justify-content: center;
    @include default-padding();
}

.global-header__ribbon__close {
    margin-left: $measure--basic;
}

.global-header {
    margin-top: $measure--small4;
    
    @include media('>=small') {
        @include standard-box();
    }

    .global-header__logo {
        grid-column-start: 1;
        grid-column-end: 4;

        @include media('<small') {
            grid-column-end: 9;
        }
    }

    // mobile & desktop  menu
    .menu {
        grid-column-end: 25;
        grid-column-start: 14;
        grid-row-start: 1;

        @include media('>=small') {
            grid-column-start: 1;
            grid-column-end: -1;
            grid-row-start: 2;
        }
    }
}

.global-header__container {
    @include default-padding();
}

.global-header__content {
    display: grid;
    @include x-column-grid(24);
    border-bottom: $border-width-thick $border-style-solid $color-raisin-black;
    padding-bottom: $measure--small2;
}

.global-header__logo {
    color: $color-purewhite;
    font-weight: $font-weight-book;
    letter-spacing: -0.5px;
    font-size: $measure--small4;
    display: inherit;
    
    display: flex;
    margin-top: 0;
    
    
    
    a {
        @include font-family('heading');
        @include heading-font-features();

        &:visited {
            color: $color-purewhite;
        }
        
        &:focus, &:active {
            @include outline-properties($color-purewhite);
        }
    }
    
    span.global-header__logo-style {
        font-weight: $font-weight-bold;
        letter-spacing: -0.5px;
    }
}

.global-header__membership-ctas {
    grid-column-end: 25;
    grid-column-start: 14;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

    @include media('<small') {
        grid-row-start: 2;
        grid-column-start: 1;
        grid-column-end: 13;
    }


    .global-header__membership-cta:first-of-type {
        margin-right: $measure--basic;
    }
}
