@mixin screenreader-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

@mixin veil() {
    background-color: rgba(30, 30, 30, 0.3);
    bottom: 0;
    display: none;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: z-index('veil');

    &[data-on='true'] {
        display: block;
    }

    @keyframes veilFadeIn {
        0% {
            background-color: rgba(30, 30, 30, 0);
            visibility: visible;
        }

        100% {
            background-color: rgba(30, 30, 30, 0.3);
        }
    }

    @keyframes veilFadeOut {
        0% {
            background-color: rgba(30, 30, 30, 0.3);
            visibility: visible;
        }

        100% {
            background-color: rgba(30, 30, 30, 0);
        }
    }
}

@mixin menu-animation-slideOpen {
    animation-duration: 0.4s;
    animation-name: slideOpen;

    @keyframes slideOpen {
        0% {
            max-height: 0;
            visibility: visible;
        }

        100% {
            max-height: 100vh;
        }
    }
}

@mixin menu-animation-slideClose {
    animation-duration: 0.4s;
    animation-name: slideClose;

    @keyframes slideClose {
        0% {
            max-height: 100vh;
            visibility: visible;
        }

        100% {
            max-height: 0;
            visibility: hidden;
        }
    }
}