@use '../../abstracts' as *;
@use '../../base' as *;
@use '../../layout' as *;

.footer-oh__wrap {
    background: linear-gradient(270deg, #AFAAA6 0%, #D51926 100%);
    
}

.footer-oh {
    padding-top: $measure--medium3;
    

    @include media('>=small') {
        @include standard-box();
        @include x-column-grid(24);
    }
}

.footer-oh__container {
    padding-bottom: $measure--small4;
    @include default-padding();
}

.footer-oh__content {
    margin-top: $measure--small4;
    display: flex;
}

.footer-oh__col {
    flex-basis: 25%;
    display: flex;
    flex-direction: column;

    span {
        margin-bottom: $measure--half;
    }
}

.footer-oh__col--cta {
    display: flex;
    align-items: self-end;
}